.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  text-decoration: none;
}

.dev-area{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.dev-page-container {
  background-color: brown;

  padding: 0.4em;
  margin: 0.3em;

  border-radius: 0.3em;

  box-shadow: 0.3em 0.6em 10px #b2b7c0a2;
}

.dev-page-container:hover {
  background-color: rgba(136, 168, 196, 0.51);
}

.dev-profile-pic-container{
  padding: 0.4em;
}

.dev-profile-pic {
  min-width: 8em;
  min-height: 6em;
  max-width: 22vw;
  max-height: 30vh;
  background-color: rgb(9, 128, 233);

  border-radius: 0.3em;
}

.dev-profile-name{
  margin: 0.2em;
}


.space-holder {
  min-height: 20vh;
  padding: 5vh;

  box-shadow: inset 0.3em 0.8em 5px rgba(0, 0, 0, 0.337);
  background-color: rgba(204, 204, 204, 0.153);

  display: flex;
  justify-content: center;
  align-items: center;
  align-content: stretch;
}